const main = () => {
  return import('./pages/main.vue');
};
const index = () => {
  return import('./pages/index.vue');
};
const node = () => {
  return import('./pages/node.vue');
};
const list = () => {
  return import('./pages/list.vue');
};
const listImg = () => {
  return import('./pages/listImg.vue');
};
const content = () => {
  return import('./pages/content.vue');
};

const service = () => {
  return import('./pages/service.vue');
};

const chart = () => {
  return import('./pages/chart.vue');
}

const rdocr = () => {
  return import('./pages/rdocr.vue');
}

const about = () => {
  return import('./pages/about.vue');
}

const notFound = () => {
  return import('./pages/404.vue');
};

const routes = [{
  path: '/',
  component: main,
  meta: {
    title: 'asdasd'
  },
  children: [{ // 
    path: '',
    component: index
  }, {
    path: ':path-:nodeid(\\d+)-:mp(\\d+)list', // 
    component: list
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)list/:cp(\\d+)', // 
    component: list
  }, {
    path: ':path-:nodeid(\\d+)node', // 
    component: node
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)img', // 
    component: listImg
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)img/:cp(\\d+)', // 
    component: listImg
  }, {
    path: '/content/:id(\\d+)', // 
    component: content
  }, {
    path: '/content_photo/:id(\\d+)', // 
    component: content
  }, {
    path: '/service',
    component: service
  }, {
    path: '/chart',
    component: chart
  }, {
    path: '/rdocr',
    component: rdocr
  }, {
    path: '/about',
    component: about
  }, {
    path: '*',
    component: notFound
  }]
}];

module.exports = routes;